// 게시글 본문
import React from 'react'
import { Link } from 'gatsby'

import styled from 'styled-components'
import './md.css'

import { useRecoilValue } from 'recoil'
import { toggleThemeAtom } from 'etc/atoms'

interface PostContentProps {
  html: string
  categories?: string[]
  page: string
}

export default function PostContent({
  html,
  categories,
  page,
}: PostContentProps) {
  const isDark = useRecoilValue(toggleThemeAtom)

  return (
    <>
      <ContentContainer>
        <Content
          dangerouslySetInnerHTML={{ __html: html }}
          className={
            isDark
              ? 'markdown-body markdown-dark'
              : 'markdown-body markdown-light'
          }
        />
      </ContentContainer>

      {page === 'project' ? (
        <>
          <Prev to={`/project`}>목록 보기</Prev>
        </>
      ) : (
        <>
          <CategoryContainer>
            <CategoryList>
              {categories?.map((categorie, idx) => (
                <CategoryItem key={idx}>{categorie}</CategoryItem>
              ))}
            </CategoryList>
          </CategoryContainer>

          <Prev to={`/tech-blog`}>목록 보기</Prev>
        </>
      )}
    </>
  )
}

const Prev = styled(Link)`
  display: block;
  width: 150px;
  margin: 120px auto 30px;
  padding: 10px 9px;
  border-radius: 30px;
  background: ${props => props.theme.tabColor};
  font-size: 16px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: ${props => props.theme.textColor};
  text-align: center;
  cursor: pointer;
  &:hover {
    color: ${props => props.theme.bgColor};
    background: ${props => props.theme.selectBoxColor};
  }

  @media screen and (min-width: 767px) {
    width: 180px;
    padding: 13px 24px;
    font-size: 18px;
    line-height: 28px;
  }
`

const CategoryItem = styled.div`
  display: inline-block;
  overflow: hidden;
  max-width: 100%;
  height: 34px;
  margin-right: 6px;
  padding: 0 15px;
  border-radius: 34px;
  font-size: 13px;
  line-height: 34px;
  color: ${props => props.theme.textColor};
  background: ${props => props.theme.tabColor};
  letter-spacing: -0.5px;
  hite-space: nowrap;
  text-overflow: ellipsis;
`

const CategoryList = styled.div`
  text-align: center;
`

const CategoryContainer = styled.div`
  max-width: 364px;
  margin: 110px auto 0;
  padding: 0 24px;

  @media screen and (min-width: 767px) {
    max-width: 630px;
  }

  @media screen and (min-width: 1439px) {
    max-width: 816px;
  }
`

const Content = styled.div`
  max-width: 364px;
  margin: 84px auto 0;
  padding: 0 24px;

  @media screen and (min-width: 767px) {
    max-width: 630px;
    margin-top: 120px;
  }

  @media screen and (min-width: 1439px) {
    max-width: 816px;
  }
  color: ${props => props.theme.postContentColor};
`

const ContentContainer = styled.div`
  max-width: 364px;
  margin: 84px auto 0;
  padding: 0 24px;

  @media screen and (min-width: 767px) {
    max-width: 630px;
    margin-top: 120px;
  }

  @media screen and (min-width: 1439px) {
    max-width: 816px;
  }
`
