import React from 'react'
import { graphql } from 'gatsby'

import Layout from 'components/Layout'
import PostHead from 'components/PostHead'
import PostContent from 'components/PostContent.tsx'

import { ItemType } from 'etc/types'

interface ProjectTemplateProps {
  location: {
    href: string
  }
  data: {
    allMarkdownRemark: {
      edges: ItemType[]
    }
  }
}

export default function ProjectTemplate({
  location: { href },
  data: {
    allMarkdownRemark: { edges },
  },
}: ProjectTemplateProps) {
  const {
    node: {
      frontmatter: {
        page,
        categories,
        startDate,
        endDate,
        title,
        subTitle,
        thumbnail: {
          childImageSharp: { gatsbyImageData },
        },
      },
      html,
    },
  } = edges[0]

  return (
    <Layout title={title} description={subTitle} url={href}>
      <PostHead title={title} page={page} gatsbyImageData={gatsbyImageData} />
      <PostContent html={html} page={page} />
    </Layout>
  )
}

export const queryMarkdownDataBySlug = graphql`
  query queryMarkdownDataBySlug($slug: String) {
    allMarkdownRemark(filter: { fields: { slug: { eq: $slug } } }) {
      edges {
        node {
          html
          frontmatter {
            page
            categories
            startDate(formatString: "YYYY.MM.DD")
            endDate(formatString: "YYYY.MM.DD")
            title
            subTitle
            thumbnail {
              childImageSharp {
                gatsbyImageData(width: 768, height: 223)
              }
            }
          }
        }
      }
    }
  }
`
