// 게시글 상단
import React from 'react'
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image'

import styled from 'styled-components'

interface PostHeadProps {
  title: string
  page: string
  date?: string
  gatsbyImageData?: IGatsbyImageData
}

export default function PostHead({
  title,
  page,
  date,
  gatsbyImageData,
}: PostHeadProps) {
  return (
    <>
      {page === 'project' ? (
        <PostHeadContainer>
          <ImageWrapper>
            {gatsbyImageData && <Img image={gatsbyImageData} alt="" />}
          </ImageWrapper>
          <TitleWrapper>{title}</TitleWrapper>
        </PostHeadContainer>
      ) : (
        <PostHeadContainer>
          <DateWrapper>
            <Date>{date}</Date>
          </DateWrapper>
          <TitleWrapper>{title}</TitleWrapper>
        </PostHeadContainer>
      )}
    </>
  )
}

const Img = styled(GatsbyImage)`
  width: 100%;
  height: 223px;
`

const ImageWrapper = styled.div``

const TitleWrapper = styled.p`
  margin-top: 12px;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.38;

  @media screen and (min-width: 767px) {
    margin-top: 24px;
    font-size: 42px;
    line-height: 1.29;
    letter-spacing: -0.8px;
  }

  @media screen and (min-width: 1439px) {
    font-size: 46px;
    line-height: 1.3;
    letter-spacing: -1px;
  }
`

const Date = styled.span`
  font-size: 13px;
  color: ${props => props.theme.postDateColor};
  letter-spacing: -0.2px;

  @media screen and (min-width: 767px) {
    font-size: 18px;
    letter-spacing: -0.5px;
  }
`

const DateWrapper = styled.div`
  line-height: 20px;
`

const PostHeadContainer = styled.div`
  max-width: 364px;
  margin: 0 auto;
  padding: 0 24px;
  text-align: center;

  @media screen and (min-width: 767px) {
    max-width: 630px;
    padding: 0;
  }

  @media screen and (min-width: 1439px) {
    max-width: 816px;
    padding: 0;
  }
`
